import Helper from '@/function.js';
import loadMore from '../../components/load-more.vue';
export default {
	data() {
		return {

			sendType: 0,
			myTasklist: [],
			token: '',
			pageIndex: 1,
			pageSize: 10,
			contentText: {
				contentdown: '上拉显示更多',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多数据了'
			},
			total: -1,
			nodata: false

		}


	},
	onShow: function() {
		this.pageIndex = 1;
		this.myTasklist = [];
		this.selType = -1;
		this.token = uni.getStorageSync('token');
		this.userInfo = uni.getStorageSync('userInfo');
		this.getMyTaskList();
	},

	onPullDownRefresh: function() {
		this.pageIndex = 1;
		this.myTasklist = [];
		this.getMyTaskList();
	},

	onReachBottom: function() {
		if (this.myTasklist.length < this.total) {
			this.pageIndex++;
			this.getMyTaskList();
		}
	},


	methods: {
		/* 调用列表自己的任务 */
		getMyTaskList() {
			var _this = this;
			var params = {
				pageIndex: _this.pageIndex,
				pageSize: _this.pageSize,
				state: _this.sendType
			};
			uni.request({
				url: _this.ApiGateWay + '/sale/Member/GetMyTaskList',
				data: params,
				method: 'POST',
				dataType: 'json',
				header: {
					Authorization: _this.token
				},
				success: function(res) {
					//console.log(res + '111');
					if (Helper.CheckData(res)) {
						let {
							errorCode,
							errorMessage,
							list,
							total,
							totalPage
						} = res;
						_this.total = res.data.total;
						_this.myTasklist = _this.myTasklist.concat(res.data.list);

						// _this.setListStatus();
					} else {

					}


				},

				fail: function() {
					uni.hideLoading();
				},
				complete: function() {
					uni.hideLoading();
					uni.stopPullDownRefresh()
				}
			});
		},
		btnClick: function(index) {
			//console.log(index);
			if (this.sendType == index) {
				return;
			}

			this.pageIndex = 1;
			this.myTasklist = [];

			this.sendType = index;
			this.getMyTaskList()
		},
		mytasorder(item) {
			console.log("==========")
			//console.log(item)
			console.log("==========")

			uni.navigateTo({
				url: '/pages/mytask/mymission?orderNo=' + item.orderNo
			})
			
		}

	}

}
